const innerAudioContext = uni.createInnerAudioContext();
innerAudioContext.autoplay = true;

var amapFile = require('../../../js/amap-wx.js');
import jsfunRecord from '@/components/jsfun-record/jsfun-record.vue'
import anUploadImg from "@/components/an-uploadImg/an-uploadImg.vue";
import banner from "@/components/banner/banner.vue";
import contact from "@/components/contact/contact.vue";
export default {
	components:{
		anUploadImg,jsfunRecord,banner,contact
	},
	data() {
		return {
			title: 'Hello',
			map:null,
			qrcode:'',
			markers:[{
				 title:'',
				 latitude: 0,
				 longitude: 0,
				 label:{
				 	content:'文本1',
				 	color:'#F76350',
				 	bgColor:'#fff',
				 	padding:5,
				 	borderRadius:4
				 }
			}],
			voicePath:''
			
		}
	},
	onLoad(option) {
		if(option.qrcode){
			this.qrcode = option.qrcode
		}
		this.getHistory();
		this.showChoice();
	},
	methods: {
		share(){
			uni.showToast({
				"title":"分享此二维码",
				"icon":"none"
			})
		},
		showChoice(){
			let _this = this;
			uni.showModal({
				"title":"提示",
				"cancelText":"已有定位",
				"confirmText":"重新定位",
				"content":"你已有定位是否重新定位？",
				"success":function(res1){
					if(res1.confirm){
						uni.navigateTo({
							"url":"/pages/address/mszc/location"
						})
						
					}else{
						
						
					}
				}
			})
		},
		openMap(){
			let _this = this;
			console.log(_this.map);
			uni.showToast({
				"title":"打开地图后请点击右下角选择导航软件",
				"icon":"none"
			})
			setTimeout(function(){
				uni.openLocation({
				    latitude: parseFloat(_this.map.longitude),
				    longitude: parseFloat(_this.map.latitude),
				    success: function () {
				        console.log('success');
				    }
				});
			},999)
			
		},
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		getMap(){
			let _this = this;
			this.$ajax("/park/detail","POST",{"qrcodeNo":_this.qrcode}).then((res)=>{
				console.log(_this.map)
				// let str = "2019-10-31T00:16:21.000+0000";
				
				for(var i in res){
					if(res[i] == null){
						res[i] = '';
					}
				}
				
				
				_this.map = res;
				// console.log(_this.map.record.length)
				// console.log(_this.map)
			})
		},
		getHistory(){
			let _this = this;
			this.$ajax("/park/page","POST",{"pageNo":1,"pageSize":3}).then((res)=>{
				console.log(res);
				_this.map = res.list[0];
			})
		},
		
		playRecord(){
			// if(res.record==''){
				
			// }
			innerAudioContext.src = this.map.record;
			// innerAudioContext.src = "https://img-cdn-qiniu.dcloud.net.cn/uniapp/audio/music.mp3";
			innerAudioContext.onPlay(() => {
			  console.log('开始播放');
			});
		},
		preview(i,type){
			let _this = this;
			var url = "";
			if(type == 0){
				
				url = _this.map.imgs
			}else{
				url = _this.map.imgs
			}
			uni.previewImage({
				urls: url
			});
		}
		
	},
	filters:{
		date(val){
			let date = val.split(".");
			date = date[0].replace("T"," ");
			return date;
		}
	}
}
